import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Button } from 'antd';
import { TableData } from 'components/common/index';
import { getStudentsForPartyMem, updatePoliticalDate } from 'services/studentForPartyMem';
import { getQueryParams } from 'utils/urlUtil';
import { dateToString } from 'utils/formatUtil';
import FormSerchStuForPartyMem from './FormSerchStuForPartyMem';
import './style.scss';

class StuPartyMemPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleSearch = values => {
    this.setState({ paramSearch: values });
  };

  handleKhaiTru = async ({ id, politicalDate }) => {
    try {
      await updatePoliticalDate({ id, politicalDate });
      this.setState({ reload: this.state.reload + 1 });
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const columns = [
      {
        title: 'Mã SV',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Giới tính',
        dataIndex: 'genderText',
        key: 'genderText',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Chuyên ngành',
        dataIndex: 'studyFieldName',
        key: 'studyFieldName',
      },
      {
        title: 'Khóa học',
        dataIndex: 'courseName',
        key: 'courseName',
      },
      {
        title: 'Tên khoa',
        dataIndex: 'facultyName',
        key: 'facultyName',
      },
      {
        title: 'Tên hệ',
        dataIndex: 'trainTypeName',
        key: 'trainTypeName',
      },
      {
        title: 'Ngày kết nạp',
        dataIndex: 'politicalDate',
        render: (text, record) => (
          <Fragment>{dateToString(record.politicalDate, 'DD/MM/YYYY')}</Fragment>
        ),
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => {
          if (record.politicalDate) {
            return (
              <Fragment>
                <Button
                  className="btnKetNapKhaiTru"
                  type="primary"
                  onClick={() => this.handleKhaiTru({ id: record.id, politicalDate: '' })}
                >
                  Khai trừ
                </Button>
              </Fragment>
            );
          }
          return (
            <Button
              className="btnKetNapKhaiTru"
              type="primary"
              href={`#party-mem/form/${record.id}`}
            >
              Kết nạp
            </Button>
          );
        },
      },
    ];
    return (
      <Card title="Đảng viên là sinh viên">
        <Row>
          <Col span={24}>
            <FormSerchStuForPartyMem onSubmit={this.handleSearch} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={getStudentsForPartyMem}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <StuPartyMemPage />
  </Page>
);



